.arrow-indicator {
  position: relative;
  text-align: center;
  width: 16px;
  height: 16px;
  transform: translate3d(0, 0, 0);
  opacity: 0.3; }
  .arrow-indicator i {
    position: absolute;
    width: 0;
    height: 16px;
    border-radius: 1px;
    background: #fff;
    transition: all 0.15s ease; }
  .arrow-indicator span i {
    height: 8px; }
  .arrow-indicator span:first-child i {
    transform-origin: 50% 0%; }
  .arrow-indicator.top, .arrow-indicator.bottom {
    opacity: 1; }
    .arrow-indicator.top i, .arrow-indicator.bottom i {
      width: 2px; }
  .arrow-indicator.top span:first-child i:first-child {
    transform: translate(0.5px, 0px) rotate(40deg);
    height: 8px; }
  .arrow-indicator.top span:first-child i:last-child {
    transform: translate(-0.5px, 0px) rotate(-40deg);
    height: 8px; }
  .arrow-indicator span:last-child {
    position: absolute;
    bottom: 0px; }
    .arrow-indicator span:last-child i {
      transform-origin: 50% 100%;
      top: -8px; }
  .arrow-indicator.bottom span:last-child i:first-child {
    transform: translate(-0.5px, 0px) rotate(40deg);
    height: 8px; }
  .arrow-indicator.bottom span:last-child i:last-child {
    transform: translate(0.5px, 0px) rotate(-40deg);
    height: 8px; }
  .arrow-indicator[data-state=disabled] {
    opacity: 0.3; }
